body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #EFF3F7;
  height: 100%;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
   padding: 50px;
   display: flex;
   justify-content: space-around;
   flex-wrap: wrap;
   font-size: 14px;
}

.Movie{
    background-color: white;
    width:40%;
    display: flex;
    justify-content: space-between;
    align-items: flex-start;
    flex-wrap: wrap;
    margin-bottom: 50px;
    text-overflow: ellipsis;
    padding: 0 20px;
    box-shadow: 0 8px 38px rgba(133, 133, 133, 0.3), 0 5px 12px rgba(133,133,0,0.22)
}

.Movie__Columns{
    width: 30%;
    box-sizing: border-box;
    text-overflow: ellipsis
}

.Movie__Columns:last-child{
    padding : 20px 0;
    width: 60%;
}

.Movie h1{
    font-size: 20px;
}

.Movie__Genres .Moive__Genre{
    margin-left: 10px;
    color: #B4B5BD;
}

.Movie .Movie__Synopsis{
    text-overflow: ellipsis;
    color: #B4B5BD;
    overflow: hidden;
}

.Movie .Movie__Poster{
    max-width: 100%;
    position: relative;
    top:-20px;
    box-shadow: -10px 19px 38px rgba(83, 83, 83, 0.3), 10px 15px 12px rgba(80,80,80,0.2)
}

@media screen and (min-width:320px) and (max-width:667px){
    .Movie{
        width:100%;
        
    }
}

@media screen and (min-width:320px) and (max-width:667px) and (orientation: portrait){
    .Movie{
        width:100%;
        flex-direction: column;
    }
    .Movie__Poster {
        top:0px;
        left:0px;
        width: 100%;
    }

    .Movie__Columns{
        width:100%!important
    }
}
